.faq {
  background-color: $color-ghost-white;
}

.faq__wrapper {
  position: relative;
  margin: 0 auto;
  padding: 120px 0;
  max-width: 1160px;

  @include vp-1199 {
    padding: 100px 0;
  }

  @include vp-767 {
    padding: 70px 0;
  }
}

.faq__title {
  margin: 0 0 39px 0;
  font-family: $ff-alumni-sans;
  font-weight: 600;
  font-size: 54px;
  line-height: 64.8px;
  letter-spacing: 1.7px;

  @include vp-1199 {
    margin: 0 7px 31px 0;
    font-size: 42px;
    line-height: 50.4px;
    letter-spacing: 1.3px;
    text-align: center;
  }

  @include vp-767 {
    margin: 0 5px 42px 0;
    font-size: 32px;
    line-height: 38.4px;
    letter-spacing: 1px;
    text-align: center;
  }
}

.faq__container {
  display: grid;
  grid-template-columns: 23.2% 76.8%;
  gap: 40px;
}

.faq__controls {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 10px;
  max-width: 260px;
}

.faq__controls .tabs__control {
  padding: 18px 0;
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;
  color: $color-sapphire;
  background-color: transparent;
  cursor: pointer;
  border: solid 1px $color-sapphire-faq;
}

.faq__controls .tabs__control.is-active{
  color: $color-default-white;
  background: $button-buy-gradient;
}
